import React, { Component, lazy, Suspense } from "react";
import { getUUID } from "../actions/fakturAction";
import { connect } from "react-redux";

import { reset } from "redux-form";
import Swal from "sweetalert2";
import {
  getReturnSupplierTemp,
  setTotalNW,
  setTotalOngkir,
} from "../actions/terimaSupplierAction";
import { getsupplierList } from "../actions/supplierAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
// import { Link } from "react-router-dom";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import { getListReturnSupplier } from "../actions/returnSupplier";
import {
  finishSend,
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import { AxiosMasterPost } from "../Axios";
import { showModal } from "../actions/ModalAction";
import Skeleton from "react-loading-skeleton";
import HeaderCard from "../HeaderCard";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import FormBarcodeReturnSupplier from "./Component/FormBarcodeReturnSupplier";
import { getToday } from "../getDate";
import { formatGram, formatRp, getBon, getUserID } from "../helper";

const FormTerimaSupplier = lazy(() => import("./Component/FormTerimaSupplier"));
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    tampungBarang: state.provinsi.returnSupplier,
    noFaktur: state.provinsi.noFaktur,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumRp: state.perhitungan.sumRp,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    onSend: state.provinsi.onSend,
  };
};
class ReturnSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      DataSupplier: [],
      kodeSupplier: "",
      namaSupplier: "",
      tanggal: getToday(),
      noReturn: "AS/RTR-S-" + getBon(),
      noBon: "AS/RTR-S-" + getBon(),
      status: "",
      totalNW: 0,
      totalOngkir: 0,
      dateStart: getToday(),
      tampungBarang: [],
      tampungBarangLocal: [],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kadar_cetak",
          text: "Cap\nKode",
        },
        {
          dataField: "qty",
          text: "Qty",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "5%" };
          },
        },
        {
          dataField: "plastik",
          text: "P",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "5%" };
          },
        },
        {
          dataField: "kulit",
          text: "K",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "5%" };
          },
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "7%" };
          },
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data),
        },
        {
          dataField: "kadar_beli",
          text: "Kode\nAwal",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data),
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data, 3),
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(RP)",
          align: "right",
          headerAlign: "right",
          formatter: (cell) => <span>Rp. {formatRp(cell)}</span>,
        },
        {
          dataField: "harga_beli",
          text: "Harga\nBeli(RP)",
          align: "right",
          headerAlign: "right",
          formatter: (cell) => <span>Rp. {formatRp(cell)}</span>,
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "ReturnSupplier",
                      rowIndex,
                      this.props.dispatch,
                      getListReturnSupplier()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      urutan: localStorage.getItem("urutan") || 0,
    };
  }

  componentDidMount() {
    localStorage.removeItem("ReturnSupplier")
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getListReturnSupplier());
    this.props.dispatch(setTotalOngkir());
    this.props.dispatch(setTotalNW());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    var barang = JSON.parse(localStorage.getItem("ReturnSupplier")) || [];
    var jenis = data.kodeJenis ? data.kodeJenis : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");
    let isi = {
      no_urut: barang.length + 1,
      kode_jenis: result_explode[0],
      keterangan: data.keterangan.toUpperCase(),
      kadar_cetak: (result_explode[2] || 0).toString(),
      kadar_beli: parseFloat(data.kadarTransaksi),
      kode_barcode: "-",
      qty: data.qty || 1,
      plastik: parseFloat(data.p) || 0,
      kulit: parseFloat(data.k) || 0,
      pkg: parseFloat(data.pkg) || 0,
      gross: parseFloat(data.gross) || 0,
      bruto: parseFloat(data.bruto) || 0,
      netto: parseFloat(data.netto) || 0,
      ongkos_rp: parseFloat(data.rp || 0),
      harga_beli: data.harga_beli === "undefined" ? 0 : data.harga_beli,
    };
    let array = JSON.parse(localStorage.getItem("ReturnSupplier")) || [];
    array.push(isi);
    localStorage.setItem("ReturnSupplier", JSON.stringify(array));
    this.props.dispatch(getListReturnSupplier());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("TerimaSupplier")))
      .then(() => this.props.dispatch(getReturnSupplierTemp()))
      .then(() => this.props.dispatch(setTotalNW()))
      .then(() => this.props.dispatch(setTotalOngkir()));
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnSupplier"));
    this.props.dispatch(getBruto("ReturnSupplier"));
    this.props.dispatch(getRp("ReturnSupplier"));
    this.props.dispatch(getPkg("ReturnSupplier"));
    this.props.dispatch(getQty("ReturnSupplier"));
  }

  kirimData() {
    var supplier = this.state.kodeSupplier
      ? this.state.kodeSupplier
      : "DEFAULT|DEFUALT";
    var explode = supplier.split("|");
    if (localStorage.getItem("ReturnSupplier") === null) {
      warning("Barang masih kosong, Mohon tambahkan barang");
      return false;
    } else if (this.state.noReturn === "") {
      warning("Mohon isi nomor Return");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi tanggal");
      return false;
    } else if (this.state.kodeSupplier === "") {
      warning("Mohon pilih supplier");
      return false;
    }
    let hasil = JSON.parse(localStorage.getItem("ReturnSupplier"));
    let total = hasil
      .map((list) => parseFloat(list.netto))
      .reduce((a, b) => a + b, 0);
    var simpan = {
      no_retur: this.props.noFaktur.toUpperCase(),
      no_bon: this.state.noReturn,
      kode_supplier: explode[0],
      total_netto: total,
      tanggal_invoice: this.state.tanggal,
      detail_barang: JSON.parse(localStorage.getItem("ReturnSupplier")),
    };
    console.log(JSON.stringify(simpan));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("ReturnSupplier"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        { content: ticket.qty, styles: { halign: "right" } },
        { content: ticket.kadar_cetak, styles: { halign: "right" } },
        { content: formatGram(ticket.plastik), styles: { halign: "right" } },
        { content: formatGram(ticket.kulit), styles: { halign: "right" } },
        { content: ticket.pkg, styles: { halign: "right" } },
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        { content: ticket.kadar_beli, styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        { content: formatRp(ticket.ongkos_rp), styles: { halign: "right" } },
        { content: formatRp(ticket.harga_beli), styles: { halign: "right" } },
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      {
        content: table
          .map((list) => parseFloat(list.qty))
          .reduce((a, b) => a + b, 0),
        styles: { halign: "right" },
      },
      "",
      "",
      "",
      {
        content: table
          .map((list) => parseFloat(list.pkg))
          .reduce((a, b) => a + b, 0),
        styles: { halign: "right" },
      },
      {
        content: formatGram(
          table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      "",
      {
        content: formatGram(
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0),
          3
        ),
        styles: { halign: "right" },
      },
      {
        content: formatRp(
          table
            .map((list) => parseFloat(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      {
        content: formatRp(
          table
            .map((list) => parseFloat(list.harga_beli))
            .reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
    ];
    // tableRows.push(footer);
    var columTable = [
      [
        { content: "No" },
        { content: "Kode Model" },
        { content: "Qty", styles: { halign: "right" } },
        { content: "Cap Kode", styles: { halign: "right" } },
        { content: "P", styles: { halign: "right" } },
        { content: "K", styles: { halign: "right" } },
        { content: "Bungkus\n(Gr)", styles: { halign: "right" } },
        { content: "Berat\n(Gr)", styles: { halign: "right" } },
        { content: "Kode Tukar", styles: { halign: "right" } },
        { content: "Murni\n(Gr)", styles: { halign: "right" } },
        { content: "Ongkos\n(Rp)", styles: { halign: "right" } },
        { content: "Harga\nBeli(Rp)", styles: { halign: "right" } },
      ],
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("trx-supplier/retur-supplier", simpan)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "no Return",
              this.state.noReturn.toUpperCase(),
              "Tanggal",
              this.state.tanggal,
              "",
              "",
              "Supplier",
              this.state.kodeSupplier.toUpperCase(),
              getUserID(),
              getToday(),
              "",
              columTable,
              "Return Barang Supplier",
              tableRows,
              footer
            )
          )
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(getReturnSupplierTemp()))
          .then(() => localStorage.removeItem("ReturnSupplier"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => localStorage.setItem("isLogin", true))
          .then(() => this.props.dispatch(getListReturnSupplier()))
          .then(() =>
            this.setState({
              noBon: "AS/RTR-S-" + getBon(),
              noReturn: "AS/RTR-S-" + getBon(),
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err?.response.data,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Return Barang Supplier" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    {/* <div className="col-lg-3">
                      <div className="text-right">
                        <Link to="/EditForm/returnSupplier">
                          <button
                            className="btn btn-warning"
                            // data-target="#editModal"
                            // data-toggle="modal"
                          >
                            Edit Return Barang
                          </button>
                        </Link>
                      </div>
                    </div> */}

                    <ModalMasterData
                      content={
                        <Suspense
                          fallback={
                            <Skeleton width={"100%"} height={50} count={5} />
                          }
                        >
                          <FormTerimaSupplier
                            from="return"
                            onSubmit={(data) => this.handleSubmit(data)}
                          />
                        </Suspense>
                      }
                      title="Tambah Data Barang"
                    />
                    <div className="col-lg-12">
                      <form id="myForm">
                        <div className="row">
                          <div className="col-lg-4 col-md-4">
                            <label htmlFor="">Supplier</label>
                            <select
                              name="supplier"
                              id="supplier"
                              className="form-control"
                              defaultValue="DEFAULT | DEFAULT"
                              onChange={this.pilihSupplier()}
                            >
                              <option value="DEFAULT | DEFAULT" key="DEFAULT">
                                Silahkan Pilih
                              </option>
                              {this.props.supplierList.map((list) => (
                                <option
                                  value={
                                    list.kode_supplier +
                                    "|" +
                                    list.nama_supplier
                                  }
                                >
                                  {list.nama_supplier}
                                </option>
                              ))}
                            </select>
                          </div>
                          <div className="col-md-4 col-lg-4">
                            <label htmlFor="">Tanggal</label>
                            <input
                              type="date"
                              id="tanggal"
                              name="tanggal"
                              defaultValue={this.state.dateStart}
                              className="form-control"
                              onChange={this.inputChange("tanggalInvoice")}
                            />
                          </div>
                          <div className="col-md-4 col-lg-4 d-none">
                            <label htmlFor="">No Return</label>
                            <input
                              type="text"
                              id="no_return"
                              name="no_return"
                              className="form-control"
                              onChange={this.inputChange("noReturn")}
                              value={this.state.noReturn}
                            />
                          </div>
                          {/* <div className="col-md-4 col-lg-4 mt-3">
                            <label htmlFor="">No Terima</label>
                            <input
                              type="text"
                              id="bon_return"
                              name="bon_return"
                              className="form-control"
                              onChange={this.inputChange("noBon")}
                              value={this.state.noBon}
                            />
                          </div> */}
                        </div>
                      </form>
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3 mb-3">
                      <div className="row">
                        <div className="col-lg-4 col-md-4"></div>
                        <div className="col-lg-4 col-md-4 mb-3">
                          <div className="text-center">
                            {this.state.kodeSupplier !== "" ? (
                              <button
                                className="btn btn-primary"
                                data-toggle="modal"
                                type="button"
                                data-target="#barcodeModal"
                              >
                                BARCODE (+)
                              </button>
                            ) : (
                              <button
                                className="btn btn-primary"
                                data-toggle="modal"
                                type="button"
                                disabled
                                data-target="#barcodeModal"
                              >
                                BARCODE (+)
                              </button>
                            )}
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              disabled={this.state.kodeSupplier === ""}
                              onClick={() => this.props.dispatch(showModal())}
                            >
                              Tambah Data
                              <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <GlobalTabel
                    keyField="id"
                    data={this.props.tampungBarang}
                    columns={this.state.columns}
                  />
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      disabled={this.props.onSend}
                      onClick={() => this.kirimData()}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="barcodeModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Barocde
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormBarcodeReturnSupplier
                  kodeSupplier={this.state.kodeSupplier}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(ReturnSupplier);
